export default {
	data() {
		return {
			modalName: null,
			keepData:[],
			note: "",
			uid:-1,
			selectedId:-1,
			todayClockIn:0
		}
	},
	methods: {
		showModal: function(e) {
			this.modalName = "DialogModal";
		},

		hideModal: function(e) {
			this.modalName = null
		},

		clockIn: function(eventId, eventTitle, today) {
			this.showModal();
			this.note = '【' + eventTitle + '】';
			if (today == 1){
				this.todayClockIn = 1;
			}
			else{
				this.todayClockIn = 0;
			}
			this.selectedId = eventId;
		},
		
		//获取用户id
		initKeep: function() {
			const _this = this;
			//获取id
			wx.login({
				success(res) {
					if (res.code) {
						//发起网络请求
						const secret = res.code;
						let uinfo = {};
						wx.getUserInfo({
							success: function(res) {
								uinfo = res.userInfo;
								uni.request({
									method: "GET",
									url: "http://localhost:8080/api/UserManage",
									header: {
										"content-type": "application/json"
									},
									data: {
										code: secret,
										gender: uinfo.gender,
										nickname: uinfo.nickName,
										avaUrl: uinfo.avatarUrl
									},
									success: (res) => {
										// console.log(res);
										_this.uid = res.data.uid;
										_this.getHabits();
									},
									fail: (res) => {
										// console.log(res);
										console.log('登录失败!');
									}
								})
							}
						});
					} else {
						console.log('登录失败！' + res.errMsg)
					}
				}
			});

		},

		//获取bigdays
		getHabits: function() {
			//根据id获取清单
			// console.log(this.uid);
			const _this = this;
			uni.request({
				method: 'GET',
				url: 'http://localhost:8080/api/habit/view',
				data: {
					uid: this.uid,
				},
				success: (res) => {
					if (res.data.err == 0) {
						let result = res.data.data;
						for (let i = 0; i < result.length; i++) {
							// console.log(result[i]);
							_this.keepData.push({
								id: result[i].id,
								title: result[i].title,
								img: result[i].img,
								time: result[i].time,
								today: result[i].today_clockin
							});
						}
					} else {
						console.log(res);
						console.log("Bad request.");
					}

				},
				fail: () => {
					console.log("Request failed.");
				}
			})
		},
		
		comfirm:function(){
			const _this = this;
			uni.request({
				method:"GET",
				url:"http://localhost:8080/api/habit/clockin",
				data:{
					uid:_this.uid,
					id:_this.selectedId
				},
				success: (res) => {
					console.log(res);
					_this.hideModal();
				},
				fail: () => {
					console.log("Bad request");
				}
			})
		},
		
		createClockin:function(){
				uni.navigateTo({
					animationType: "slide-in-right",
					url: "./createKeep"
				})
		},
	},
	mounted: function() {
		this.$nextTick(function() {
			this.initKeep();
		})
	},
	onPullDownRefresh() {
		//监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
		this.keepData = [];
		this.initKeep();
	}
}
